.div-table {
	display: table;  
	width: 1110px;
	background-color: #f7f4e4;         
	border: 1px solid black;
}
.div-tr {
	display: table-row;
	width: auto;
	border: 1px solid black; 
}
.div-th, .div-td {
	float: left;
	display: table-column;         
	width: 220px; 
	height: 26px;
	border: 1px solid black; 
	text-align: center;
}

.div-th {
	font-weight: bold;
	background-color: #cac4a9;
}



